@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-left {
    text-align: start;
}
@mixin flex-end {
    justify-content: flex-end;
}
@mixin flex-start {
    justify-content: flex-start;
}

.section-banner-column {
	.section-content-inner {
		align-items: center;
		padding-bottom: 50px;
		border-bottom: 1px solid var(--miscellaneous-color);
	}
	.banner-content {
		margin-top: 40px;
	}
	.block-text {
		font-size: 14px;
		margin-bottom: 30px;
		h2,h3 {
			line-height: 1.2em;
		}
		p {
			margin: 14px 0;
			strong {
				font-weight: 500;
			}
		}
		.heading-content {
			margin-bottom: 30px;
		}
	}
	.block-svg_content {
		margin-bottom: 25px;
		.flex >.inline-flex {
			align-items: center;
			@include text-left;
			&:not(:last-child) {
				@include margin-end(20px);
			}
		}
		.icon {
			@include padding-end(10px);	
			svg {
				width: 20px;
				height: 20px;
			}
		}
		.text {
			font-size: 13px;
		}
	}
	@media (min-width: 1000px) {
		.section-content-inner {
			padding-bottom: 70px;
		}
		.banner-content {
			margin-top: 0;
		}
		.block-text {
			p {
				margin-top: 0;
			}
		}
	}
}
.page-about {
	.section-banner-column {
		.banner-content {
			max-width: 525px;
		}
		.block-svg_content .text {
			font-size: inherit;
		} 
	}
}